@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.organization-info {
  background-color: var(--secondary-color);
  width: clamp(360px, 50vw, 600px);
  height: 80px;
  padding: 22px;
  border-radius: 15px;
  margin-bottom: -20px;
}

.organization-info-inner {
  width: clamp(110px, 50vw, 210px);
  height: 100%;
  align-items: center;
  justify-content: space-around;
}

.avatar {
  width: 80px;
  height: 80px;
  border-radius: 100%;
}

.search-bar {
  background-color: var(--secondary-color);
  width: clamp(360px, 50vw, 600px);
  height: 40px;
  padding: 10px 22px;
  border-radius: 15px;
  transition: 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  margin-bottom: 10px;
}

.search-bar:hover {
  background-color: var(--highlight-color);
}

.search-bar-input {
  background: none;
  border: none;
  outline: none;
  width: 100%;
  height: 100%;
  font-size: 20px;
  color: var(--text-color);
}

::placeholder {
  color: var(--secondary-text-color);
}

form {
  width: 100%;
  align-items: center;
}

.submit-button {
  background-color: var(--secondary-text-color);
  padding: 7px 8px;
  border: none;
  outline: none;
  border-radius: 100px;
  cursor: pointer;
  transition: background-color 0.5s cubic-bezier(0.075, 0.82, 0.165, 1);
}

.submit-button:hover {
  background-color: var(--text-color);
}

.submit-icon {
  font-size: 21px;
  color: var(--secondary-color);
}

.loading-spinner-container {
  display: flex;
  justify-content: center;
  margin: 20px 0;
}

.spinner {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 8px solid rgba(0, 0, 0, 0);
  border-top-color: var(--secondary-text-color);
  animation: loading 1s linear infinite;
}

@keyframes loading {
  to {
    transform: rotate(360deg);
  }
}

#Repositories {
  width: 100%;
  min-height: 500px;
}

#repository {
  background-color: var(--secondary-color);
  width: clamp(360px, 50vw, 600px);
  min-height: 120px;
  padding-left: 30px;
  justify-content: space-around;
  transition: 0.6s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  animation: appear 0.5s 1;
}

@keyframes appear {
  from {
    transform: translateY(100%);
  }
  to {
    transform: translateY(0%);
  }
}

#repository:hover {
  background-color: var(--highlight-color);
}

.repo-info {
  padding: 3px 0;
  margin: 2px 0;
}

.repo-title {
  font-weight: bold;
  font-size: 22px;
}

.repo-url {
  font-size: 14px;
  color: var(--secondary-text-color);
}

.repo-lower {
  width: clamp(150px, 50vw, 180px);
  height: 50px;
  justify-content: flex-start;
  align-items: center;
}

.repo-count {
  font-weight: bold;
  font-size: 20px;
  margin-right: 15px;
}

.repo-icon {
  font-size: 20px;
  margin-right: 5px;
}

.page-numbers-container {
  width: 100%;
  padding: 10px;
}

.page-number {
  margin: 0 6px;
  padding: 5px 8px;
  cursor: pointer;
  border-radius: 7px;
}

.active {
  background-color: var(--secondary-color);
}

:root {
  --primary-color: #18181a;
  --secondary-color: #242427;
  --highlight-color: #27272a;
  --text-color: #bcbbcc;
  --secondary-text-color: #6d6c79;
}

html,
body {
  font-family: Roboto, sans-serif;
  background-color: var(--primary-color);
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
}

a {
  color: var(--text-color);
  text-decoration: none;
}

.App {
  width: 100%;
  min-height: 100vh;
  color: var(--text-color);
  font-size: 20px;
}

.flex-column {
  display: flex;
  flex-direction: column;
}

.flex-row {
  display: flex;
  flex-direction: row;
}

.centered {
  justify-content: center;
  align-items: center;
}

.container {
  padding: 25px;
  margin: 10px;
  border-radius: 15px;
}

.main {
  width: 60%;
  min-height: 500px;
}

.header-text {
  text-align: center;
  letter-spacing: 7px;
  font-weight: lighter;
  font-size: clamp(10px, 2vw, 20px);
  margin: 50px 0;
  animation: blink 3s infinite;
}

@keyframes blink {
  0% {
    color: var(--text-color);
  }
  40% {
    color: var(--text-color);
  }
  40.2% {
    color: var(--secondary-text-color);
  }
  60% {
    color: var(--secondary-text-color);
  }
  60.2% {
    color: var(--text-color);
  }
}

